import { Injectable } from '@angular/core';
import { Post } from '../models/post';
import { BehaviorSubject, Observable, throwError } from 'rxjs';
import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { tap, catchError } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class PostService {

  private apiUrl = 'http://localhost:8090';

  private _posts: BehaviorSubject<Post[]>;
  private dataStore: {
    posts: Post[]
  };

  constructor(private http: HttpClient) {
    this.dataStore = { posts: [] };
    this._posts = new BehaviorSubject<Post[]>([]);
  }

  addPost(post: Post): Observable<Post> {
    return this.http.post<Post>(this.apiUrl + '/posts', post).pipe(
      tap((posts: Post) =>  console.log('save post')),
      catchError(this.handleError)
    );
  }


  getPost(id: number): Observable<Post> {
    return this.http.get<Post>(this.apiUrl + '/posts/' + id).pipe(
      tap(data => console.log('post:' + JSON.stringify(data)))
    );
  }

  getPosts(): Observable<Post[]> {
    return this.http.get<Post[]>(this.apiUrl + '/posts').pipe(
        tap(data => console.log('All:' + JSON.stringify(data))),
        catchError(this.handleError)
    );
  }



  private handleError (err: HttpErrorResponse) {
    // in real world  app, we may send the server to some remote logging infrastructure
    // instead of logging it to console
    let errorMessage = '';

    if (err.error instanceof ErrorEvent) {
        // A client-side or network error occured. Handle it accordingly
        errorMessage = `An error ocurred : ${err.error.message}`;
    } else {
        // the backed returned an unusuccesfull response code.
        // the response body may contain clues as to what went wrong
        errorMessage = `Server returned code: ${err.status}, error message is: ${err.message}`;
    }

    console.error(errorMessage);
    return throwError(errorMessage);
  }
}
