import { Component, OnInit, Input, Output, EventEmitter, OnChanges, ViewChild, AfterViewInit } from '@angular/core';
import { MatTableDataSource, MatDialog, MatPaginator, MatSort } from '@angular/material';
import { Post } from '../models/post';
import { PostDialogComponent } from './post-dialog.component';
import { PostService } from '../services/post.service';

@Component({
  selector: 'app-posts',
  templateUrl: './post-list.component.html',
  styleUrls: ['./post-list.component.scss']
})
export class PostListComponent  implements OnInit {
  posts: Post[] = [];
  errorMessage: string;

  constructor(private postService: PostService,  public dialog: MatDialog) {
  }

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  displayedColumns = ['datePosted', 'title', 'category', 'delete'];
  dataSource: MatTableDataSource<Post>;

  openDialog(): void {
    const dialogRef = this.dialog.open(PostDialogComponent, {
      height: '768px',
      width: '1024px',
      data: 'Add Post'
    });
    dialogRef.componentInstance.event.subscribe((result) => {
      this.postService.addPost(result.data).subscribe((data) => {
        // This code will be executed when the HTTP call returns successfully
        this.postService.getPosts().subscribe(
          posts => {
            this.dataSource = new MatTableDataSource<Post>(posts);
          },
          error => this.errorMessage = <any>error
        );
      });
    });
  }

  ngOnInit() {
    this.postService.getPosts().subscribe(
      posts => {
        this.dataSource = new MatTableDataSource<Post>(posts);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      },
      error => this.errorMessage = <any>error
    );
  }

  applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // MatTableDataSource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }
}
