import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';

import { MaterialModule } from '../shared/material.module';
import { FlexLayoutModule } from '@angular/flex-layout';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';

import { DashboardAppComponent } from './dashboard-app.component';
import { ToolbarComponent } from './components/toolbar/toolbar.component';
import { MainContentComponent } from './components/main-content/main-content.component';
import { SidenavComponent } from './components/sidenav/sidenav.component';
import { UserService } from './services/user.service';
import { HttpClientModule } from '@angular/common/http';
import { NotesComponent } from './components/notes/notes.component';
import { NewContactDialogComponent } from './components/new-contact-dialog/new-contact-dialog.component';
import { PostListComponent } from './posts/post-list.component';
import { PostDetailComponent } from './posts/post-detail.component';
import { EventRegistrationComponent } from './event-registration/event-registration.component';
import { EventRegistrationDetailComponent } from './event-registration/event-registration-detail.component';
import { PostDialogComponent } from './posts/post-dialog.component';
import { CKEditorModule } from '@ckeditor/ckeditor5-angular';
import { SafePipe } from '../shared/safe-pipe.pipe';


declare var $: any;

const routes: Routes = [
  {
    path: '', component: DashboardAppComponent,
    children: [
      { path: 'posts', component: PostListComponent },
      { path: 'posts/:id', component: PostDetailComponent },
      { path: 'event-registration', component: EventRegistrationComponent },
      { path: 'event-registration/:id', component: EventRegistrationDetailComponent },
      { path: '', component: PostListComponent }

    ]
  },
  { path: '**', redirectTo: '' }
];

@NgModule({
  imports: [
    CommonModule,
    HttpClientModule,
    MaterialModule,
    FlexLayoutModule,
    FormsModule,
    ReactiveFormsModule,
    RouterModule.forChild(routes),
    CKEditorModule
  ],
  providers: [
    UserService,
  ],
  declarations: [
    DashboardAppComponent,
    ToolbarComponent,
    MainContentComponent,
    SidenavComponent,
    NotesComponent,
    NewContactDialogComponent,
    PostListComponent,
    PostDetailComponent,
    EventRegistrationComponent,
    EventRegistrationDetailComponent,
    PostDialogComponent,
    SafePipe
  ],
  exports: [
    SafePipe
  ],
  entryComponents: [
    NewContactDialogComponent,
    PostDialogComponent
  ]
})
export class DashboardModule { }
